import React from "react"
import TodoItem from "./components/TodoItem"
import AddTodo from "./components/AddTodo"
import nextId from "react-id-generator";
import Nav from "./components/Nav"
import About from "./components/About"
import {BrowserRouter as Router, Route} from "react-router-dom"


class App extends React.Component{
  constructor(){
    super()
    this.state = {
     todos:[]
      
    }
    this.handleOnChange = this.handleOnChange.bind(this)
  }

  handleOnChange(id){
    this.setState(prevState => {
      const update = prevState.todos.map(todo =>{
        if (todo.id === id){
          todo.completed = !todo.completed
        }
        return todo 
      })
      return{
        todos : update
      }
    })
  }

  handleAdd = (text) =>{
    const newitem = {id:nextId(), text, completed:false }
    this.setState({todos: [...this.state.todos, newitem]})
  }

  handleDelete = (id) =>{
    this.setState({todos:[...this.state.todos.filter(todo =>
      todo.id !== id
    )]})
  }

 
  render(){
    const item = this.state.todos.map(todo => <TodoItem key={todo.id} item={todo} del={this.handleDelete} onchange={this.handleOnChange} />)
    return(
      <Router>
        <div >
        <Nav />
        <Route exact path="/" render={props => (
          <React.Fragment>
            <AddTodo onchange={this.handleChange} addTodo={this.handleAdd} />
        {this.state.todos.length == 0 ? <h2 style={{color:"grey", marginLeft: "10px"}}>No todos yet</h2> : item}
          </React.Fragment>
        )} />
        <Route path="/about" render={props => (
          <div>
            <About />
          </div>
        )} />
        
        
      </div>
      </Router>
      
    )
  }
}

export default App