import React from "react"
import "../App.css"

function TodoItem(props){
    const checkStyle = {
        margin:"15px"
    }
    const textStyle = {
        color:"grey",
        textDecoration: "line-through",
    }
    return(
        <div className="todo">
            <div className="item">
                <input
                style={checkStyle} 
                type="checkbox"
                checked={props.item.completed}
                onChange={()=>props.onchange(props.item.id)}
                />
                <p style={props.item.completed ? textStyle : null}>{props.item.text}</p>
                <button onClick={()=>props.del(props.item.id)}>x</button>
            </div>
        </div>
    )
}

export default TodoItem